import React, { useEffect, useRef } from "react";
import * as PIXI from "pixi.js";
import Visual from "./visual";

const AstroAnimation = () => {
    const canvasRef = useRef(null);

    useEffect(() => {
        // Pixi  
        const app = new PIXI.Application({
            resizeTo: window,
            backgroundColor: 0x0d1b2a, //   
            antialias: true,
        });

        // canvas HTML 
        if (canvasRef.current) {
            canvasRef.current.appendChild(app.view);
        }

        //    
        createStars(app, 200); // 200  

        const visual = new Visual();

        const onResize = () => {
            visual.show(app.screen.width, app.screen.height, app.stage);
        };

        window.addEventListener("resize", onResize);
        onResize();

        app.ticker.add(() => {
            visual.animate();
        });

        return () => {
            window.removeEventListener("resize", onResize);
            app.destroy(true, true);
        };
    }, []);

    //   
    const createStars = (app, count) => {
        for (let i = 0; i < count; i++) {
            const star = new PIXI.Graphics();
            star.beginFill(0xffffff); //  
            star.drawCircle(0, 0, Math.random() * 1.5); //   
            star.endFill();

            star.x = Math.random() * app.screen.width;
            star.y = Math.random() * app.screen.height;

            app.stage.addChild(star);
        }
    };

    return <div ref={canvasRef} style={{ width: "100%", height: "100vh" }}></div>;
};

export default AstroAnimation;
