import React, { useEffect, useRef } from 'react';
import WebFont from 'webfontloader';  // WebFontLoader import
import { Visual } from '../main/visual';  // main  visual.js 
import { Text } from '../main/text';      // main  text.js 

function MainPage() {
    const canvasRef = useRef(null);
    const textRef = useRef(null);
    const visualRef = useRef(null);
    useEffect(() => {
        const canvas = canvasRef.current;
        const ctx = canvas.getContext('2d');
        const pixelRatio = window.devicePixelRatio > 1 ? 2 : 1;
        //     
        WebFont.load({
            google: {
                families: ['Hind:700'],
            },
            fontactive: () => {
                textRef.current = new Text(); //   
                window.addEventListener('resize', resize);
                resize();
                window.requestAnimationFrame(animate); //   
            },
        });
        function resize() {
            const stageWidth = document.body.clientWidth;
            const stageHeight = document.body.clientHeight;
            canvas.width = stageWidth * pixelRatio;
            canvas.height = stageHeight * pixelRatio;
            ctx.scale(pixelRatio, pixelRatio);
            // 'Hello earthling'     
            const pos = textRef.current.setText('Hello earthling', 6, stageWidth, stageHeight);
            visualRef.current = new Visual(pos, stageWidth, stageHeight); // Visual    
        }
        function animate() {
            requestAnimationFrame(animate); //  
            if (visualRef.current) {
                visualRef.current.animate(ctx); // Visual  
            }
        }
        return () => {
            window.removeEventListener('resize', resize);
        };
    }, []);
    return (
        <div>
            <canvas ref={canvasRef} style={{ width: '100%', height: '100vh', backgroundColor: 'black' }}></canvas>
        </div>
    );
}

export default MainPage;